import { TabBar } from 'antd-mobile'
import React, { Component } from 'react'

export default class Information extends Component {
    render() {
        return (
            <div>
                <div>
                    <img style={{width:"100%"}} src={require('../image/2.jpg')}/>
                    <i className='iconfont icon-fanhui' onClick={()=>{this.props.history.push('/home');}}></i>
                    <i className='iconfont icon-gouwuche' ></i>
                </div>
                <div style={{marginTop:"230px"}}>
                    <button style={{background:"#f5f4f9",border:"0px white solid",paddingRight:"10px"}}>
                        <i className='iconfont icon-dianpu'></i>
                        <p style={{marginTop:"5px"}}>店铺</p>
                    </button>
                    <button style={{background:"#f5f4f9",border:"0px white solid",paddingRight:"10px"}}>
                        <i className='iconfont icon-taobaokefu-01'></i>
                        <p style={{marginTop:"5px"}}>客服</p>
                    </button>
                    <button style={{background:"#f5f4f9",border:"0px white solid",paddingRight:"10px",paddingLeft:"10px"}}>
                        <i className='iconfont icon-shoucang'></i>
                        <p style={{marginTop:"5px"}}>收藏</p>
                    </button>
                    <button style={{width:"100px",height:"40px",border:"0px white solid",background:"#ff9705",borderTopLeftRadius:"20px",borderBottomLeftRadius:"20px",color:"white",marginLeft:"30px",marginTop:"6px"}}>
                        加入购物车
                    </button>
                    <button style={{width:"100px",height:"40px",border:"0px white solid",background:"#ff5a08",borderTopRightRadius:"20px",borderBottomRightRadius:"20px",color:"white"}}>
                        立即购买
                    </button>
                </div>
            </div>
        )
    }
}
