import React from 'react';
import './App.css';
import {BrowserRouter as Router,Route} from 'react-router-dom';
// import Mytab from './pages/Mytab';
// import { SearchBar, Switch } from 'antd-mobile';
import Home, { Details, Lunbo } from './pages/Home';
import Cart from './pages/Cart';
import greenTab from './pages/greenTab';
import yellowTab from './pages/yellowTab';
import Information from './pages/Information';
import SearchBarExample from './pages/SearchBar';

const App=()=>{
  return (
    <Router>
      <Route exact path='/home' component={Home} />
      <Route path='/cart' component={Cart} />
      <Route path='/greenTab' component={greenTab} />
      <Route path='/yellowTab' component={yellowTab} />
      <Route path='/information' component={Information}></Route>
      <Route path='/searchbar' component={SearchBarExample}></Route>
    </Router>
  )
}

export default App;
